<template>
  <div class="icon-container" v-on:click="onClick" :class="{'btn': btn}">
    <i :data-feather="name" class="icon"></i>
  </div>
</template>

<script>
const feather = require('feather-icons')
export default {
  mounted () {
    feather.replace()
  },
  props: ['name', 'onClickIcon', 'btn', 'item', 'link'],
  methods: {
    onClick () {
      if (this.btn) {
        this.onClickIcon()
      }
      if (this.link) {
      let folder_name = this.item.name.replace(/ /g, '-')
      this.$router.push({name: 'Reader', params: {id: this.item.id, folder: folder_name}})
      }
    }
  }
}
</script>



