<template>
  <ul class="section-list center-item">
    <li class="section-item" v-for="item in list">
      <feather-icon name="file-text" :item="item" link="true"/>
      <div class="section-item-info">
        <div class="section-item-title" v-on:click="onClickTitle(item.id, item.name)">
           {{item.name}}</div>
        <div class="section-item-author">
          <span class="form-tag" v-for="t in item.tags" v-on:click="onClickTag(t)">#{{t}}</span>
        </div>
      </div>
    </li>
  </ul>
</template>

<script>
import {folders} from '../json/seed'
import FeatherIcon from './FeatherIcon'

export default {
  props: ['list'],
  components: {
    'feather-icon': FeatherIcon
  },
  methods: {
    onClickTitle (id, folder) {
      let folder_name = folder.replace(/ /g, '-')
      this.$router.push({name: 'Reader', params: {id: id, folder: folder_name}})
    },
    onClickTag (tag) {
      this.$router.push({name: 'Search', query: {q: `#${tag}`}})
    }
  }
}
</script>



