<template>
  <nav class="navbar">
    <div class="navbar-content">
      <div class="navbar-content-left">
        <router-link :to="{name: 'Home'}"><feather-icon name="home"/></router-link>
        <feather-icon name="arrow-left" btn="true" :onClickIcon="onClickBack"/>
        <!-- <feather-icon name="settings"/> -->
        <feather-icon name="file-plus" btn="true" :onClickIcon="onClickCreate"/>
      </div>

      <div class="navbar-content-right">
        <navbar-search />
        <div class="navbar-navbar-menu-btn-container" v-if="isEdit">
          <feather-icon name="more-vertical" btn="true" :onClickIcon="onClickMenu"/>
        </div>
        <navbar-menu v-show="showMenu" v-if="isEdit" :onClickDismiss="showMenu ? onClickDismiss : null" :onClickDelete="onClickDelete"/>

      <delete-modal v-if="showDeleteModal" :onClickDismiss="onClickDismissDelete"/>
      </div>
    </div>
  </nav>
</template>

<script>
import FeatherIcon from './FeatherIcon'
import NavbarMenu from './NavbarMenu'
import NavbarSearch from './NavbarSearch'
import DeleteModal from './DeleteModal'

export default {
  name: 'navbar',
  components: {
    'feather-icon': FeatherIcon,
    'navbar-menu': NavbarMenu,
    'navbar-search': NavbarSearch,
    'delete-modal': DeleteModal
  },
  data () {
    return {
      showMenu: false,
      isEdit: false,
      showDeleteModal: false
    }
  },
  created () {
    this.isEdit = this.$route.name == 'Reader'
  },
  methods: {
    onClickMenu () {
      this.showMenu = !this.showMenu
    },
    onClickBack () {
      this.$router.go(-1)
    },
    onClickCreate () {
      this.$router.push({name: 'Create'})
    },
    onClickDismiss () {
      this.showMenu = false
    },
    onClickDelete () {
      this.showDeleteModal = true
        this.onClickDismiss()
    },
    onClickDismissDelete () {
      this.showDeleteModal = false
    }
  },
  watch: {
    '$route.name': function (v) {
        this.isEdit = v == 'Reader'
    }
  }
}
</script>



