<template>
  <div>
  <ul class="navbar-menu" v-on-click-outside="onClickOutside">
    <li class="navbar-menu-item" v-on:click="onClickEdit">Edit</li>
    <li class="navbar-menu-item" v-on:click="onClickDelete">Delete</li>
  </ul>
  </div>
</template>

<script>
export default {
  props: ['onClickDismiss', 'onClickDelete'],
  methods: {
    onClickEdit () {
      this.$router.push({name: 'Edit', params: {id: this.$route.params.id}})
        this.onClickDismiss()
    },
    onClickOutside () {
      if (this.onClickDismiss) {
        this.onClickDismiss()
      }
    },
  }
}
</script>



