<template>
  <div class="modal-container">
  <div class="modal" v-on-click-outside="onClickDismiss">
    <div class="modal-title">{{title}} <div class="modal-dismiss btn" v-on:click="onClickDismiss">&times;</div></div>
    <div class="modal-content">
      <slot name="content"></slot>
    </div>
  </div>  
  </div>  
</template>

<script>
export default {
  props: ['title', 'onClickDismiss']
}
</script>



