<template>
  <div id="app">
    <navbar v-show="!$store.state.settings.readMode"/>
    <div class="content">
      <router-view/>
    </div>
  </div>
</template>

<script>
import Navbar from './components/Navbar'
export default {
  name: 'App',
  components: {
    'navbar': Navbar
  },
  created () {
    this.$store.dispatch('getDocs')
    this.$store.dispatch('getSettings')
  }
}
</script>

<style lang="sass">
  @import './assets/css/app.scss'
</style>



