<template>
  <modal title="Content" :onClickDismiss="onClickDismiss">
    <div slot="content">
      <ul>
        <li></li>
      </ul>
    </div>
  </modal>
</template>

<script>
import Modal from '../layouts/Modal'
export default {
  components: {
    'modal': Modal
  },
  props: ['onClickDismiss']
}
</script>



