const utils = {
  wordCount: function (str) {
    return str ? str.split(' ').filter(w => w != '').length : 0
  },
  // split text by number of words
  splitText: function (str, count) {
    let result = []
    let words = str.split(' ').filter(w => w != '')
    let i_start = 0
    let i_end = count
    while (i_end < words.length) {
      result.push(words.slice(i_start, i_end).join(' '))
      i_start += count
      i_end += Math.min(words.length, count)
    }
    return result
  },
  wordsArray: function (str) {
    return str.split(' ').filter(w => w != '')
  },
  estimateWordCount: function (width, height) {
    let b_w = 0.358061034
    let b_h = 0.618485125
    let c = -353.1592236
    var factor = width < 700 ? 0.75 : 0.95
    factor = 1
    return Math.floor((width*(b_w)+height*(b_h)+c)*factor)
  },
  textToPages: function (str, count, temp) {
    let result = []
    let words = str.split(' ').filter(w => w != '')
    let i_start = 0
    let i_end = count
    if (words.length > count) {
      while (i_start < words.length) {
        result.push(words.slice(i_start, i_end).join(' '))
        i_start += count
        i_end = Math.min(words.length, i_end + count)
      }
    } else {
      result.push(str)
    }
    temp.loading = false
    return result
  },
  guid: function () {
    return Math.random().toString(36).substr(2, 9)
  }
}

export default utils



