const LSServices = {
  getDocs: function () {
    let docs = localStorage.getItem('docs')
    return JSON.parse(docs || '[]')
  },
  setDocs: function (docs) {
    let str = JSON.stringify(docs)
    localStorage.setItem('docs', str)
  },
  setSettings: function (settings) {
    let str = JSON.stringify(settings)
    localStorage.setItem('settings', str)
  },
  getSettings: function () {
    let docs = localStorage.getItem('settings')
    return JSON.parse(docs || '{}')
  }
}

export default LSServices


