import LSServices from '../../services/LocalStorageServices'

// initial state
const state = {
  docs: [],
  doc: null
}

// getters - functions for computing derived state based on store state
// ie. getById
const getters = {

}
var timer;
// actions - functions that commit mutations; can contain arbitrary asynchronous operations
const actions = {
  getDocs ({state, commit}) {
    commit('getDocs')
  },
  addDoc ({state, commit}, doc) {
    commit('addDoc', doc)
  },
  getDoc ({state, commit}, id) {
    commit('getDoc', id)
  },
  updateDoc ({state, commit}, doc) {
    commit('updateDoc', doc)
  },
  deleteDoc ({state, commit}, id) {
    commit('deleteDoc', id)
  }
}

// mutations - functions that change state in store; can't call them directly
const mutations = {
  getDocs (state) {
    let docs = LSServices.getDocs()
    state.docs = docs
  },
  addDoc (state, doc) {
    state.docs.push(doc)
    LSServices.setDocs(state.docs)
  },
  getDoc (state, id) {
    let docs = state.docs.length > 0 ? state.docs.filter(d => d.id == id) : []
    if (docs.length == 1) {
      state.doc = docs[0]
    }
  },
  deleteDoc (state, id) {
    state.docs = state.docs.length > 0 ? state.docs.filter(d => d.id != id) : []
    LSServices.setDocs(state.docs)
  },
  updateDoc (state, doc) {
    let newDocs = state.docs.map(d => {
      // find doc, update it
      if (d.id == doc.id) {
        return {
          ...d,
          ...doc
        }
      }
      return d
    })
    state.docs = newDocs

    LSServices.setDocs(newDocs)
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}


