import LSServices from '../../services/LocalStorageServices'

// initial state
const state = {
  readMode: true,
  bkgColor: '#fff',
  lineSpacing: 'normal'
}

// getters - functions for computing derived state based on store state
// ie. getById
const getters = {

}
var timer;
// actions - functions that commit mutations; can contain arbitrary asynchronous operations
const actions = {
  toggleReadMode ({state, commit}) {
    commit('toggleReadMode')
  },
  offReadMode ({state, commit}) {
    commit('offReadMode')
  },
  onReadMode ({state, commit}) {
    commit('onReadMode')
  },
  getSettings ({state, commit}) {
    commit('getSettings')
  },
  setBkgColor ({state, commit}, color) {
    commit('setBkgColor', color)
  },
  setLineSpacing ({state, commit}, lineSpacing) {
    commit('setLineSpacing', lineSpacing)
  }
}

// mutations - functions that change state in store; can't call them directly
const mutations = {
  toggleReadMode (state) {
    state.readMode = !state.readMode
  },
  offReadMode (state) {
    state.readMode = false
  },
  onReadMode (state) {
    state.readMode = true
  },
  getSettings (state) {
    let settings = LSServices.getSettings()
    if (settings && settings.bkgColor) {
      state.bkgColor = settings.bkgColor
      state.lineSpacing = settings.lineSpacing
    }
  },
  setBkgColor (state, color) {
    state.bkgColor = color
    LSServices.setSettings(state)
  },
  setLineSpacing (state, lineSpacing) {
    state.lineSpacing = lineSpacing
    LSServices.setSettings(state)
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}


