<template>
  <div class="reader-bar">
    <div class="reader-bar-left">
      <!-- <div class="reader-bar-content btn" v-on:click="onClickContent">GO TO</div> -->
    </div>
    <div class="reader-bar-right">
      <feather-icon name="sliders" :onClickIcon="onClickSettings" btn="true"/>
      <!-- <feather-icon name="bookmark" /> -->
    </div>
    <settings v-if="showSettings" :onClickDismiss="onClickDismiss"/>
    <content-modal v-if="showContentModal" :onClickDismiss="onClickDismissContent"/>
  </div>
</template>

<script>
import FeatherIcon from './FeatherIcon'
import Settings from './Settings'
import ContentModal from './ContentModal'
export default {
  components: {
    'feather-icon': FeatherIcon,
    'settings': Settings,
    'content-modal': ContentModal
  },
  data () {
    return {
      showSettings: false,
      showContentModal: false,
    }
  },
  methods: {
    onClickSettings () {
      this.showSettings = true
    },
    onClickDismiss () {
      this.showSettings = false
    },
    onClickDelete () {
      this.showDeleteModal = true
    },
    onClickDismissDelete () {
      this.showDeleteModal = false
    },
    onClickContent () {
      this.showContentModal = true
    },
    onClickDismissContent () {
      this.showContentModal = false
    }
  }
}
</script>



