<template>
  <modal title="Delete" :onClickDismiss="onClickDismiss">
    <div slot="content">
      <div>Are you sure you want to delete?</div>
      <div class="delete-modal-btns">
        <button class="btn delete-modal-btn" v-on:click="onClickYes">Yes</button>
        <button class="btn delete-modal-btn" v-on:click="onClickNo">No</button>
      </div>
    </div>
  </modal>
</template>

<script>
import Modal from '../layouts/Modal'
export default {
  components: {
    'modal': Modal
  },
  props: ['onClickDismiss'],
  methods: {
    onClickNo () {
      this.onClickDismiss()
      this.$store.dispatch('onReadMode')
    },
    onClickYes () {
      this.$store.dispatch('deleteDoc', this.$route.params.id)
      this.$router.push({name: 'Home'})
      this.onClickDismiss()
    }
  }
}
</script>



