<template>
  <modal title="Settings" :onClickDismiss="onClickDismiss">
    <div slot="content">
      <div class="settings-section-title">Background color</div>
      <ul class="color-selector">
        <li class="color-item">
          <div class="color-btn-container" 
            :class="{'color-btn-container-active': color.orange == bkgColor}"
            v-on:click="onSetColor('orange')">
            <div class="color-btn bkg-orange"></div>
          </div>
        </li>
        <li class="color-item">
          <div class="color-btn-container" 
            :class="{'color-btn-container-active': color.green == bkgColor}"
            v-on:click="onSetColor('green')">
            <div class="color-btn bkg-green"></div>
          </div>
        </li>
        <li class="color-item">
          <div class="color-btn-container" 
            :class="{'color-btn-container-active': color.yellow == bkgColor}"
            v-on:click="onSetColor('yellow')">
            <div class="color-btn bkg-yellow"></div>
          </div>
        </li>
      </ul>
      <hr>
      <div class="settings-section-title">Line spacing</div>
      <ul class="line-spacing-selector">
        <li class="line-spacing-item"><div class="line-spacing-container" v-on:click="onSetLineSpacing(1.4)" :class="{'line-spacing-container-active': lineSpacing == 1.4}">1.4</div></li>
        <li class="line-spacing-item"><div class="line-spacing-container" v-on:click="onSetLineSpacing(1.6)" :class="{'line-spacing-container-active': lineSpacing == 1.6}">1.6</div></li>
        <li class="line-spacing-item"><div class="line-spacing-container" v-on:click="onSetLineSpacing(1.8)" :class="{'line-spacing-container-active': lineSpacing == 1.8}">1.8</div></li>
      </ul>
    </div>
  </modal>
</template>

<script>
import Modal from '../layouts/Modal'
export default {
  components: {
    'modal': Modal
  },
  props: ['onClickDismiss'],
  data () {
    return {
      color: {
        orange: '#ffa500',
        green: '#66cdaa',
        yellow: '#ffd700'
      }
    }
  },
  methods: {
    onSetColor (key) {
      this.$store.dispatch('setBkgColor', this.color[key])
    },
    onSetLineSpacing (space) {
      this.$store.dispatch('setLineSpacing', space)
    }
  },
  computed: {
    bkgColor () {
      return this.$store.state.settings.bkgColor
    },
    lineSpacing () {
      return this.$store.state.settings.lineSpacing 
    }
  }
}
</script>



