<template>
  <div class="content">
    <div class="search-results center-item mobile-item">
      <h1>Results for "{{q}}"</h1>
      <placeholder v-if="results.length == 0" />
      <section-list :list="results" />
    </div>
  </div>
</template>

<script>
import Placeholder from '../components/Placeholder'
import SectionList from '../components/SectionList'
export default {
  components: {
    'placeholder': Placeholder,
    'section-list': SectionList
  },
  data () {
    return {
      q: '',
      results: []
    }
  },
  created () {
    this.$store.dispatch('offReadMode')
    this.q = this.$route.query.q
    this.search()
  },
  methods: {
    search () {
      this.results = []
      if (this.q.includes('#')) {
        let q = this.q.replace('#', '')
        this.results = this.$store.state.docs.docs.filter(d => d.tags.map(t => t.toLowerCase()).includes(q.toLowerCase()) )
      } else {
        this.results = this.$store.state.docs.docs.filter(d => d.name.toLowerCase().includes(this.q.toLowerCase()) )
      }
    }
  },
  watch: {
    '$route.query.q': function (v) {
      this.q = v
      this.search()
    }
  }
}
</script>



