<template>
  <div class="document-create-container">
    <h1>Edit Document</h1>
    <div class="document-create-form">
      <input type="text" placeholder="File name" class="input form-input" v-model="doc.name" :class="{'form-error-input': errors.name}" @keyup="onTyping" autofocus>
      <div class="form-error">{{errors.name}}</div>
      <textarea cols="30" rows="10" class="input form-textarea" placeholder="Type your content" v-model="doc.content" :class="{'form-error-input': errors.content}" @keyup="onTyping"></textarea>
      <div class="form-error">{{errors.content}}</div>
      <div class="form-tags input mobile-item">
        <span class="form-tag" v-for="(tag, i) in tags" v-on:click="onRemove(i)">{{`#${tag}`}}</span> <input type="text" class="form-tag-input" :placeholder="tags.length == 0 ? '#tags' : ''" v-model="doc.tags" 
          @keyup.space="onTypeTags">
      </div>
      <button class="document-create-submit" v-on:click="onSubmit">Update</button>
    </div>
  </div>
</template>

<script>
import utils from '../api/utils'
export default {
  data () {
    return {
      tags: [],
      doc: {
        name: '',
        content: '',
        tags: ''
      },
      errors: {
        name: undefined,
        content: undefined,
        tags: undefined
      }
    }
  },
  created () {
      this.$store.dispatch('offReadMode')
      // get doc
      this.$store.dispatch('getDoc', this.$route.params.id)
      let d = this.$store.state.docs.doc
      if (d && d.id == this.$route.params.id) {
        this.doc = {
          id: d.id,
          name: d.name,
          content: d.content
        }
        this.tags = d.tags
      }
  },
  methods: {
    onTyping () {
      if (this.doc.name != '') {
        this.errors.name = undefined
      }
      if (this.doc.content != '') {
        this.errors.content = undefined
      }
    },
    onSubmit () {
      let {name, content, id} = this.doc
      let tags = this.tags
      let doc = {
        id,
        name,
        content,
        tags
      }

      // validation
      if (name == '') {
        this.errors.name = 'This field is required'
      }
      if (content == '') {
        this.errors.content = 'This field is required'
      }
      // submit
      if (!this.errors.name && !this.errors.content) {
        this.$store.dispatch('updateDoc', doc)
        this.$router.push({name: 'Home'})
      }
    },
    onTypeTags () {
      let tags = this.doc.tags.split(' ').filter(t => t != '')
      this.tags = this.tags.concat(tags)
      this.doc.tags = ''
    },
    onRemove (i) {
      this.tags.splice(i, 1)
    }
  },
  watch: {
    '$store.state.docs.doc': function (v) {
      if (v && v.id == this.$route.params.id) {
        this.doc = {
          id: v.id,
          name: v.name,
          content: v.content
        }
        this.tags = v.tags
      }
    }
  }
}
</script>


