<template>
  <div class="navbar-search">
    <feather-icon name="search" />
    <input type="text" placeholder="Search" @keyup.enter="onSubmit" v-model="q">
  </div>
</template>

<script>
import FeatherIcon from './FeatherIcon'
export default {
  components: {
    'feather-icon': FeatherIcon
  },
  data () {
    return {
      q: ''
    }
  },
  methods: {
    onSubmit () {
      this.$router.push({name: 'Search', query: {q: this.q}})
    }
  }
}
</script>



