<template>
  <div>
    <section-title name="documents" :count="$store.state.docs.docs.length"/>
    <section-list :list="$store.state.docs.docs" v-if="$store.state.docs.docs.length > 0"/>
    <place-holder v-if="$store.state.docs.docs.length == 0" />
  </div>
</template>

<script>
import SectionTitle from '../components/SectionTitle'
import SectionList from '../components/SectionList'
import Placeholder from '../components/Placeholder'
export default {
  components: {
    'section-list': SectionList,
    'section-title': SectionTitle,
    'place-holder': Placeholder,
  },
  created () {
    this.$store.dispatch('offReadMode')
  }
}
</script>



