<template>
  <div>
    <reader-bar  v-show="!$store.state.settings.readMode"/>
    <div 
      class="reader-content" 
      :style="{background: bkgColor}"
      v-on:click="onClick" 
      v-on:dblclick="onDoubleClick">
      <div v-if="loading">Loading</div>
      <div class="reader-content-text" ref="readerText" id="readerText" v-if="!loading" :style="{lineHeight: lineSpacing}">
        {{page}}
      </div>

      <div class="reader-progress" v-if="!loading">
        <div class="reader-progress-word-count">
          {{currentWords}} words
        </div>
        <div class="reader-progress-page">
          {{current_page+1}} of {{pages.length}}
        </div>
        <div class="reader-progress-word-count-percent">
          {{percentRead}}%
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import utils from '../api/utils'
import {sample_text} from '../json/seed'
import ReaderBar from '../components/ReaderBar'
export default {
  components: {
    'reader-bar': ReaderBar
  },
  data () {
    return {
      width: window.innerWidth,
      height: window.innerHeight,
      text: '',
      charCount: 0,
      wordCount: 0,
      input: '',
      pages: [],
      current_page: 0,
      loading: true,
      words: [],
      currentWords: 0
    }
  },
  created () {
    this.$store.dispatch('onReadMode')
    this.loading = true
    this.$store.dispatch('getDoc', this.$route.params.id)
    let d = this.$store.state.docs.doc
    if (d && d.id == this.$route.params.id) {
      this.wordCount = utils.wordCount(d.content)
      this.text = d.content
    }
  },
  methods: {
    onClick (e) {
      let positionX = e.clientX
      let isHighlight = window.getSelection().toString() != ''
      if (!isHighlight && window.getSelection().type != 'Range') {
        if (positionX > 0.8*this.width && this.current_page < this.pages.length-1) {
          this.current_page += 1
        } else if (positionX < 0.2*this.width && this.current_page > 0) {
          this.current_page -= 1
        } else {
          this.$store.dispatch('toggleReadMode')
        }
      } else {
          this.$store.dispatch('onReadMode')
      }
    },
    onDoubleClick () {
      window.getSelection().removeAllRanges()
    },
    renderText () {
      this.loading = true
      let node = document.getElementById('readerText')
      $('#readerText').text('')
      node.textContent = ''
      let line = ''
      let iteration = 1
      while (node.offsetWidth < this.width-60 || node.offsetHeight < this.height-142) {
        
        line += 'The '
        node.innerHTML += 'The '
        // break at 2000 iteration
        if (iteration == 2000) {
          break;
        }
        iteration += 1
        // record the index of the word you stopped

        // stop loading
        if (node.offsetHeight == this.height-142) {
          this.loading = false
        }
      }
    },
    renderTextByWordCount () {
      let count = utils.estimateWordCount(this.width, this.height)
      this.current_page = 0
      this.loading = true
      this.pages = utils.textToPages(this.text, count, this)
      this.currentWords = utils.wordCount(this.pages[this.current_page])
    }
  },
  mounted () {
    this.renderTextByWordCount()
    let that = this
    window.onresize = function (e) {
      that.width = window.innerWidth
      that.height = window.innerHeight
      that.current_page = 0
      that.renderTextByWordCount()
    }
  },
  computed: {
    page () {
      return this.pages[this.current_page] 
    },
    percentRead () {
      return Math.ceil(this.currentWords*(this.current_page+1)*100/((this.pages.length)*this.currentWords))
    },
    bkgColor () {
      return this.$store.state.settings.bkgColor
    },
    lineSpacing () {
      return this.$store.state.settings.lineSpacing 
    }
  },
  watch: {
    '$store.state.docs.doc': function (v) {
      this.text = v.content
    }
  }
}
</script>



