import Vue from 'vue'
import Vuex from 'vuex'
// modules
import settings from './modules/settings'
import docs from './modules/docs'

Vue.use(Vuex)

const debug = process.env.NODE_ENV !== 'production'

export default new Vuex.Store({
  modules: {
    settings,
    docs
  },
  strict: debug
})


